import React, {Component} from 'react'
import one from '../images/1.png'

class CanvasActual extends Component {


  render() {
    return (
	<div id="canvasWeb" >
	<h1 style={{position: "absolute", top: "5px"}}>Canvas Web</h1>
	<div id="beginCanvasActual" style={{marginTop: "75px"}}>
		<div id="canvasBlackOuter" style={{width: "40vw", height: "50vh", border: "15px solid black", background: "beige"}}>
			<img style={{padding: "30px", width: "100%", height: "100%"}} src={one} />
		</div>
		</div>
	</div>
    )
  }
}
export default CanvasActual



